





















import Vue from "vue";

export default Vue.extend({
  props: {
    mobileNavbarVisible: {
      type: Boolean,
      required: true,
    },
  },
  methods: {
    navigateMobile(routeName: string): void {
      this.$emit("closeMobileNavBar");
      this.navigate(routeName);
    },
    navigate(routeName: string): void {
      if (routeName !== this.$route.path) {
        this.$router.push(routeName);
      }
    },
  },
});
